___CSS_LOADER_EXPORT___.push([module.id, ".vs-dialog{overflow-x:hidden;overflow-y:auto}", "",{"version":3,"sources":["webpack://./resources/js/admin/pages/dashboard/categories/parts/updateModal.vue"],"names":[],"mappings":"AAwUA,WACA,iBAAA,CACA,eACA","sourcesContent":["<template>\n  <vs-dialog v-model=\"active\" :loading=\"loading\" full-screen overflow-hidden>\n    <template #header>\n      <h4 class=\"not-margin\">\n        {{ $t('categories.create-modal.title') }}\n      </h4>\n    </template>\n\n\n    <div class=\"row\">\n      <div class=\"col-12 col-md-6 col-lg-4\">\n        <vs-input v-model=\"form.ru.name\" :placeholder=\"$t('categories.inputs.name')\">\n          <template #icon>\n            ru\n          </template>\n        </vs-input>\n\n        <vs-input v-model=\"form.en.name\" :placeholder=\"$t('categories.inputs.name')\" class=\"mt-2\">\n          <template #icon>\n            en\n          </template>\n        </vs-input>\n\n        <vs-select\n          v-model=\"form.category_id\"\n          :placeholder=\"$t('categories.inputs.parent')\"\n          class=\"mt-2\"\n          filter\n        >\n          <template v-if=\"form.errors.has('category_id')\" #message-danger>\n            {{ form.errors.get('category_id') }}\n          </template>\n\n          <vs-option v-for=\"category in categories\"\n                     :key=\"category.id\"\n                     :label=\"category.translations.find(e => e.locale === locale).name\"\n                     :value=\"category.id\"\n          >\n            {{ category.translations.find(e => e.locale === locale).name }}\n          </vs-option>\n        </vs-select>\n      </div>\n\n      <div class=\"col-lg-8 col-md-6 mt-2 mt-md-0\">\n        <div class=\"vs-input-parent vs-input-parent--state-null vs-input-content vs-component--primary\">\n          <textarea v-model=\"form.ru.description\" :placeholder=\"$t('categories.inputs.description')\"\n                    class=\"vs-input vs-input--has-icon\"\n          />\n          <span class=\"vs-input__icon\">\n            ru\n          </span>\n        </div>\n\n        <div class=\"vs-input-parent vs-input-parent--state-null vs-input-content mt-2 vs-component--primary\">\n          <textarea v-model=\"form.en.description\" :placeholder=\"$t('categories.inputs.description')\"\n                    class=\"vs-input vs-input--has-icon\"\n          />\n          <span class=\"vs-input__icon\">\n            en\n          </span>\n        </div>\n      </div>\n      <div class=\"col-12 col-md-6 mt-3\">\n        <h4 class=\"ps-2\">PDF</h4>\n        <vue-dropzone id=\"dropzonePDF\" :options=\"dropzoneOptionsPDF\" :use-custom-slot=\"true\">\n          <div class=\"dropzone-custom-content\">\n            <h3 class=\"dropzone-custom-title\">Drag and drop to upload content!</h3>\n            <div class=\"subtitle\">...or click to select a file from your computer</div>\n          </div>\n        </vue-dropzone>\n      </div>\n      <div class=\"col-12 col-md-6 mt-3\">\n        <h4 class=\"ps-2\">Photo</h4>\n        <vue-dropzone id=\"dropzonePhoto\" :options=\"dropzoneOptionsPhoto\" :use-custom-slot=\"true\">\n          <div class=\"dropzone-custom-content\">\n            <h3 class=\"dropzone-custom-title\">Drag and drop to upload content!</h3>\n            <div class=\"subtitle\">...or click to select a file from your computer</div>\n          </div>\n        </vue-dropzone>\n      </div>\n    </div>\n\n    <template #footer>\n      <div class=\"row justify-content-center\">\n        <div class=\"col-12 col-md-auto\">\n          <vs-button block size=\"large\" @click=\"store\">\n            {{ $t('form.save') }}\n          </vs-button>\n        </div>\n      </div>\n    </template>\n  </vs-dialog>\n</template>\n\n<script>\nimport {getCategories} from \"~/admin/api/categories\";\nimport Vue from \"vue\";\nimport Form from \"vform\";\nimport {mapGetters} from \"vuex\";\n\n\nimport vue2Dropzone from 'vue2-dropzone'\nimport 'vue2-dropzone/dist/vue2Dropzone.min.css'\nimport axios from \"axios\";\nimport { removeImage } from \"~/admin/api/images\"\nimport { getCategory } from \"~/admin/api/categories\"\n\nexport default {\n  name: \"UpdateModal\",\n  components: {\n    vueDropzone: vue2Dropzone\n  },\n  props: {\n    bus: {\n      type: Vue,\n      required: false,\n      default: () => {\n        return new Vue()\n      }\n    },\n    id: {\n      type: Number,\n      required: false,\n      default: undefined\n    }\n  },\n  data: () => ({\n    active: false,\n    loading: false,\n    form: new Form({\n      ru: {\n        name: '',\n        description: ''\n      },\n      en: {\n        name: '',\n        description: ''\n      },\n      category_id: '',\n      file: null,\n      image: null\n    }),\n    categories: []\n  }),\n  computed: {\n    ...mapGetters({\n      locale: 'lang/locale',\n      token: 'auth/token'\n    }),\n\n    dropzoneOptionsPDF() {\n      let self = this\n      return {\n        paramName: \"pdf\",\n        acceptedFiles: \".pdf\",\n        url: '/api/admin/categories/pdf/upload',\n        thumbnailWidth: 150,\n        maxFilesize: 5,\n        maxFiles: 1,\n        dictRemoveFile: self.locale === 'ru' ? '' : 'Remove',\n        addRemoveLinks: true,\n        headers: {\n          'Authorization': `Bearer ${self.token}`,\n          'Accept-Language': self.locale,\n        },\n        init: function () {\n          this.on(\"error\", function (file, message) {\n            alert(message);\n            return 0;\n          });\n          this.on('success', function (file, response) {\n            console.log(response)\n            self.form.file = response.payload.name\n            file.previewElement.dataset.name = self.form.file\n          })\n          this.on(\"removedfile\", function (file) {\n            if (file.previewElement.dataset.name === self.form.file) {\n              self.removePDF(self.form.file)\n              self.form.file = null\n            } else {\n              alert(Error)\n              return 0;\n            }\n          })\n        }\n      }\n    },\n    dropzoneOptionsPhoto() {\n      let self = this\n      return {\n        paramName: \"image\",\n        acceptedFiles: \"image/jpg, image/png, image/jpeg\",\n        url: '/api/admin/image/upload',\n        thumbnailWidth: 150,\n        maxFilesize: 5,\n        maxFiles: 1,\n        dictRemoveFile: self.locale === 'ru' ? '' : 'Remove',\n        addRemoveLinks: true,\n        headers: {\n          'Authorization': `Bearer ${self.token}`,\n          'Accept-Language': self.locale,\n        },\n        init: function () {\n          this.on(\"error\", function (file, message) {\n            alert(message);\n            return 0;\n          });\n          this.on('success', function (file, response) {\n            console.log(response)\n            let image = response.payload.images.pop();\n            console.log(image)\n            self.form.image = image.id\n            file.previewElement.dataset.id = image.id\n          })\n          this.on(\"removedfile\", function (file) {\n            console.log(file.previewElement.dataset.id, self.form.image)\n            if (Number(file.previewElement.dataset.id) === self.form.image) {\n              removeImage(self.form.image)\n                .then(r => {\n                  console.log(r)\n                  this.form.image = null\n                })\n                .catch(e => {\n                  console.log(e)\n                })\n            }\n          })\n        }\n      }\n    }\n  },\n  watch: {\n    locale: function (newVal) {\n      document.querySelectorAll('.dz-remove').forEach(e => {\n        e.innerHTML = newVal === 'ru' ? '' : 'Remove'\n      })\n    }\n  },\n  mounted() {\n    this.bus.$on('open', (id) => {\n      this.active = true\n      this.loading = true\n\n      getCategory(id)\n        .then(category => {\n          this.form = new Form({\n            ru: {\n              name: category.translations.find(el => el.locale === 'ru').name,\n              description: category.translations.find(el => el.locale === 'ru').description\n            },\n            en: {\n              name: category.translations.find(el => el.locale === 'en').name,\n              description: category.translations.find(el => el.locale === 'en').description\n            },\n            image: category.image ? category.image.id : null,\n            category_id: category.parents ? category.parents[0] ? category.parents[0].id : '' : '',\n          })\n\n          this.loading = false\n        })\n      .catch(() => {\n        this.$vs.notification({\n          duration: 4000,\n          sticky: true,\n          position: 'top-right',\n          color: 'danger',\n          title: this.$t('notification.get.danger.title'),\n          text: this.$t('notification.get.danger.text'),\n        })\n\n        this.active = false\n        this.loading = false\n      })\n\n    })\n\n    getCategories()\n      .then(r => {\n        this.categories = r\n      })\n\n    if (this.id) {\n      console.log(this.id)\n      this.form.category_id = this.id\n    }\n  },\n  methods: {\n    removePDF(name) {\n      axios.delete('/api/admin/categories/pdf/' + name)\n    },\n    store() {\n      this.form.post('/api/admin/categories/')\n        .then(r => {\n          console.log(r)\n          this.$vs.notification({\n            duration: 2000,\n            sticky: true,\n            position: 'top-right',\n            color: 'success',\n            title: this.$t('notification.store.success.title'),\n            text: this.$t('notification.store.success.text'),\n          })\n\n          this.form = new Form({\n            ru: {\n              name: '',\n              description: ''\n            },\n            en: {\n              name: '',\n              description: ''\n            },\n            category_id: '',\n            file: null,\n            image: null\n          })\n\n          this.$parent.get()\n          this.active = false\n\n        })\n    }\n  }\n\n}\n</script>\n\n<style>\n.vs-dialog {\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n</style>"],"sourceRoot":""}]);
